.Share {
  font-size: var(--text-size);
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1em;
}

.Share__button {
  opacity: 0.5;
  -webkit-transition: opacity 0.4s ease-out;
  -o-transition: opacity 0.4s ease-out;
  transition: opacity 0.4s ease-out;
  cursor: pointer;
  margin-right: 1px;
}

.Share__button:hover {
  opacity: 1;
}



